<template>
  <div class="container-h">
    <div class="row chat-container">
      <div :key="i" v-for="(message,i) in this.messages" class="col s12 message" :class="{'user': message.user}">
        <div class="avatar">
        </div>
        <div v-if="!message.display" class="bubble">
          <div class="loading">
            <span class="dot" style="animation-delay: 0s"></span>
            <span class="dot" style="animation-delay: 0.05s"></span>
            <span class="dot" style="animation-delay: 0.1s"></span>
          </div>
        </div>
        <div v-if="message.display" class="bubble">
          {{ message.message }}
        </div>
      </div>

      <div v-show="!this.answering" class="col s12 message user">
        <div class="avatar">
        </div>
        <div class="choices">
          <div :key="option.id" class="bubble" v-for="option in this.currentOption" @click="selectOption(option)">
            {{ option.title }}
          </div>
          <div v-if="previousOptionsStack.length > 0" @click="back" class="bubble">
            Go Back
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Messager",
  data() {
    return {
      answering: false,
      messages: [],
      currentOption: {},
      mapOption: [
        {
          "id": "Option_1",
          "title": "How can i contact you?",
          "answer": ["Idk Mail?", "Maybe  PhoneNumber"],
          "options": [{
            "id": "Option_1_2",
            "title": "How can i contact you through Linkedin?",
            "answer": ["Of course this Of course this Of course thisOf course thisOf course thisOf course thisOf course thisOf course thisOf course this "],
            "options": [{
              "id": "Option_1_2_3",
              "title": "How can i contact you through Linkedin IN THIS PAGE?",
              "answer": ["Of course this Of course this Of course thisOf course thisOf course thisOf course thisOf course thisOf course thisOf course this "],
            }]
          }]
        },
        {
          "id": "Option_2",
          "title": "What can i call you?",
          "answer": ["IDK Ming Duong ?"]
        }
      ],
      previousOptionsStack: []
    }
  },
  methods: {
     async selectOption(option) {
       if(this.answering) {
         return
       }
      this.answering = true
       const timer = ms => new Promise(res => setTimeout(res, ms))

      let loading = async (option) => {
        this.messages.push({
          "display": true,
          "message": option.title,
          "user": true,
        })
        for (let i = 0; i < option.answer.length; i++)  {
          let a = option.answer[i]
          this.messages.push({
            "display": false
          })

          await timer(3000)

          this.messages.pop()
          this.messages.push({
            "message": a,
            "display": true
          })
          await timer(30)
          continue
        }
        if (option != null) {
          this.previousOptionsStack.push(this.currentOption)
          if (option.options != null) {
            this.currentOption = option.options
          }
        }
        this.answering = false
       }

       loading(option)
    },
    back() {
       console.log()
       this.currentOption = this.previousOptionsStack.pop()
    }
  }, mounted() {
    this.currentOption = this.mapOption
  }
}
</script>

<style scoped>

@keyframes jump {
  0% {
    transform: translate3d(0, 10%, 0);
  }
  40% {
    transform: translate3d(0, -30%, 0);
  }
  100% {
    transform: translate3d(0, -60%, 0);
  }
}

.dot {
  height: 8px;
  width: 8px;
  background-color: #5318f3;
  border-radius: 50%;
  margin: 0 5px;
  display: inline-block;
  animation: jump .3s linear alternate infinite;
}

.loading {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.container-h {
  background: #5318f3;
  position: absolute;
  top: 20%;
  left: 40%;
  height: 500px;
  width: 400px;
  position: relative;
  z-index: inherit;
}

@keyframes message {
  0% {
    max-height: 100vmax;
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    max-height: 100vmax;
    overflow: visible;
  }
}

.message {
  display: flex;
  flex-direction: row;
  padding: 10px 10px !important;
}

.avatar {
  min-height: 40px;
  min-width: 40px;
  max-height: 40px;
  max-width: 40px;
  background: #f31818;
}

.bubble {
  font-size: 16px;
  max-width: calc(100% - 80px);
  padding: 5px 10px;
  margin: 0px 8px;
  background: #00d4d5;
  border-radius: 10px;
  color: #5318f3;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  text-align: start;
  transform: scale(0);
  animation: message 0.15s ease-out 0s forwards;
}

.chat-container {
  height: 100%;
  overflow-y: scroll;
}

.user {
  flex-direction: row-reverse !important;
  align-items: end;
}

.choices {
  display: flex;
  flex-direction: column;
  align-items: end;
}

.choices .bubble {
  margin-top: 10px;
}

.choices .bubble:hover {
  margin-top: 10px;
  background: white;
  cursor: url("data:image/x-icon;base64,"), auto;
}

</style>