<template>
  <div :class="{'active':true}" class="container-noti container" id="background" @click.prevent="CloseNoti($event)">
    <div class="row" >
      <div class="noti-long col s12">
        <div class="icon">
          <img class="icon-img" src="../assets/notifications/news.png"/>
        </div>
        <div class="content-noti">
            <div class="header">
              BREAKING NEWS!!!
            </div>
          <div class="news_text">⠀⠀⠀⠀⠀⠀
          </div>
        </div>
      </div>
      <div class="noti-long col s12 ">
        <div class="icon">
        </div>
        <div class="content-noti">
        </div>
      </div>
      <div class="noti-small col l6 12">
        <div class="calender">
          <div>
          <h4 class="date">{{ this.Date.date }}</h4>
          <h3 class="day">{{ this.Date.day }}</h3>
            <h4 class="month">{{ this.Date.month }}</h4>
          </div>
          <div class="event">Nothing particular :3</div>
        </div>
      </div>
      <div class="noti-small col l6 12">
        <div class="weather">
          <h3 class="place">{{ `Hanoi` }} </h3>
          <div>
          <h2 class="tempu">25° </h2>
          <h5 class="status">Hopefully Sunny :)</h5>
          </div>
        </div>
      </div>
      <div class="noti-long-big col s12">
        <FlappyBird></FlappyBird>
      </div>
    </div>
  </div>
</template>

<script>
import FlappyBird from "@/components/notification_app/FlappyBird";

export default {
  name: "Nofication",
  components: {FlappyBird},
  computed: {
    Date() {
      const monthNames = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];

      const date = {
        0: 'Monday',
        1: 'Tuesday',
        2: 'Wednesday',
        3: 'Thursday',
        4: 'Friday',
        5: 'Saturday',
        6: 'Sunday'
      }
      let a = new Date()
      return {
        'date': date[a.getDay() - 1],
        'month':  monthNames[a.getMonth()],
        'day': a.getDate()
      }
    }
  },
  data() {
    return {
      noti: true
    }
  },
  methods: {
    CloseNoti() {
      this.noti = !this.noti
    }
  }
}
</script>

<style scoped>
