<script>

export default {
  name: "Navbar",
  components: {
  },
  data() {
    return {
      time: Date,
      choice: {
        finder: true,
        file: true,
        edit: false
      },
      finderT: '',
      spotify: true
    }
  },
  methods: {
    openFinder(bool) {
      this.choice.finder = bool
    },
    openFile(bool) {
      this.choice.file = bool
    },
    openSpotify(...bool) {
      if (bool.length == 0) {
        this.spotify = !this.spotify
        return
      }
      this.spotify = bool[0]
    }
  },
  computed: {
    date() {
      return this.time.toString().split(" ").reduce((d, element, i) => {
        if (i < 3) {
          return d + " " + element
        }
        return d
      }, "")
    },
    actualTime() {
      return this.time.toString().split(" ")[4]
    },
  }, mounted() {
    this.time = new Date()
    setInterval(() => {
      this.time = new Date()
    }, 1000)
  }
}
</script>

