<template>
  <div class="game" ref="background" @click.prevent="startGame">
    <div v-if="!gameStarted" class="message">
      Press Enter To Start Game
    </div>

    <img v-if="gameStarted" ref="bird" :style="{bottom: birdY + 'px'}" class="bird"
         src="../../assets/flappyBird/subject.png" alt="bird-img">

    <template v-for="(pipe,i) in pipes">
      <div :key="i" ref="pipe" :class="{'pipe': true, 'pipe-bottom': !pipe.topPipe}" :style="{left: pipe.x + '%',height: pipe.height + '%'}"></div>
    </template>

    <div class="score">
      <span class="score_title"></span>
      <span class="score_val"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "FlappyBird",
  data: () => {
    return {
      gameStart: false,
      moveSpeed: 0.8,
      gravity: 0.25,
      birdY: 40,
      frameId: '',
      acceleration: 0,
      topHeight: 0,
      score: 0,
      lastPipe:0,
      pipes: [{
        topPipe: true,
        x: 50,
        height: 35,
      },
        {
          topPipe: false,
          x: 50,
          height: 35,
        }]
    }
  }, methods: {
    startGame() {
      if (!this.gameStart) {
        this.gameStart = true
        requestAnimationFrame(this.apply_gravity)
        requestAnimationFrame(this.movingPipe)
        requestAnimationFrame(this.createPipes)
        return
      }
      this.acceleration = this.acceleration + 5

      if (this.acceleration > 5) {
        this.acceleration = 5
      }

    },
    apply_gravity() {
      if (!this.gameStart) {
        return
      }

      if (this.birdY > this.topHeight) {
        this.birdY = this.topHeight - 30
      }

      this.acceleration = this.acceleration - this.gravity

      if (this.acceleration < -1.5) {
        this.acceleration = -1.5
      }

      if (this.birdY + this.acceleration > this.topHeight - 30) {
        this.birdY = this.topHeight - 30
      } else {
        this.birdY = this.birdY + this.acceleration
      }

      if (this.birdY < 0) {
        this.gameStart = false
        this.resetBirdPosition()
        return;
      }
      requestAnimationFrame(this.apply_gravity)
    },
    movingPipe() {
      if (!this.gameStart) {
        return
      }

      let background = this.$refs.background.getBoundingClientRect();
      let bird_props = this.$refs.bird.getBoundingClientRect();
      let pipes = this.$refs.pipe

      pipes.forEach(pipe => {
        let pipe_sprite_props = pipe.getBoundingClientRect()
        if (
            bird_props.left < pipe_sprite_props.left +
            pipe_sprite_props.width &&
            bird_props.left +
            bird_props.width > pipe_sprite_props.left &&
            bird_props.top < pipe_sprite_props.top +
            pipe_sprite_props.height &&
            bird_props.top +
            bird_props.height > pipe_sprite_props.top
        ) {
          this.gameStart = false
          this.resetBirdPosition()
          return
        } else {
          if (
              pipe_sprite_props.right < bird_props.left &&
              pipe_sprite_props.right + this.moveSpeed*background.width/100 >= bird_props.left
          ) {
            this.score = this.score + 1;
            console.log(this.score)
          }
        }
      })

      this.pipes = this.pipes.reduce((updatedPipes,pipe) => {
        if (pipe.x > -10) {
          updatedPipes.push({...pipe, x: pipe.x - this.moveSpeed})
        }

        return updatedPipes
      },[])

      requestAnimationFrame(this.movingPipe)
    },
    createPipes() {
      if( !this.gameStart) {
        return
      }
      let background = this.$refs.background.getBoundingClientRect();

      this.lastPipe = this.lastPipe + 1
      if (this.lastPipe > background.width * 2/3) {
        let topHeight = Math.floor(Math.random() * 51) + 10
        let bottomHeight = 100 - topHeight - 30

        this.pipes.push(
          {
            topPipe: true,
            x: 110,
            height: topHeight,
          },
            {
              topPipe: false,
              x: 110,
              height: bottomHeight,
            }
        )

        this.lastPipe = 0
      }

      requestAnimationFrame(this.createPipes)
    },
    resetBirdPosition() {
      let background = this.$refs.background.getBoundingClientRect();
      this.birdY = background.height / 2
      this.pipes = []
      this.lastPipe = 0
      this.acceleration = 0
    }
  }, computed: {
    gameStarted() {
      return this.gameStart
    }
  },
  mounted() {
    let background = this.$refs.background.getBoundingClientRect();
    this.topHeight = background.height
    this.resetBirdPosition()
  }

}
</script>

<style scoped>

.pipe-bottom {
  background: url('../../assets/flappyBird/hotdog_bottom.png') !important;
  bottom: 0;
  width: 30px;
  background-size: cover !important;;
  background-repeat: no-repeat !important;;
  background-position: center top !important;
}

.pipe {
  background: url('../../assets/flappyBird/hotdog_top.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;
  position: absolute;
  width: 30px;
  z-index: 50;
}

.game {
  height: 100%;
  width: 100%;
  background: url('../../assets/flappyBird/background.png');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  cursor: pointer;
  user-select: none;
  overflow: hidden;
}

.bird {
  height: 15%;
  position: absolute;
  left: 30px;
  z-index: 50;
}

.pipe_sprite {
  position: fixed;
  top: 40%;
  left: 100%;
  height: 70%;
  width: 6%;
  background-color: green;
}

.message {
  position: relative;
  z-index: 10;
  height: 10%;
  font-size: 1.7rem;
  font-weight: 100;
  color: white;
  top: 35%;
  right: 0%;
  text-align: center;
}

.score {
  position: absolute;
  z-index: 10;
  height: 10%;
  font-size: 10px;
  font-weight: 100;
  color: goldenrod;
  top: 0;
  left: 0;
}

.score_val {
  color: gold;
}
</style>
